import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators, NgForm } from '@angular/forms'
import { Shortener } from './shortener';

@Component({
  selector: 'app-shortener',
  templateUrl: './shortener.component.html',
  styleUrls: ['./shortener.component.css']
})
export class ShortenerComponent implements OnInit {

  baseUrl: string = 'http://localhost:8080/';
  shortUrl: string = '';
  FormData: FormGroup;
  constructor(private builder: FormBuilder, private shortener: Shortener) { }

  ngOnInit(): void {
    this.FormData = this.builder.group({
      LongUrl: new FormControl('', [Validators.required]),

    });
  }

  onSubmit(FormData) {

    console.log(FormData)
    this.shortener.PostMessage(FormData.LongUrl)
      .subscribe(response => {
        var shortenerResponseJson = JSON.parse(response)
        this.shortUrl = this.baseUrl + shortenerResponseJson.shortUrl
        console.log(this.shortUrl)

      }, error => {
        console.warn(error.responseText)
        console.log({ error })
      })
  }






}
